.NavbarLinksGroup-module__control__t-YWe {
  font-weight: 500;
  display: block;
  width: 100%;
  padding: var(--mantine-spacing-xs) var(--mantine-spacing-md);
  color: var(--mantine-color-text);
  font-size: var(--mantine-font-size-sm);
}

  @media (hover: hover) {

  .NavbarLinksGroup-module__control__t-YWe:hover {
    background-color: var(--mantine-color-gray-0);
  }
    [data-mantine-color-scheme='dark'] .NavbarLinksGroup-module__control__t-YWe:hover {
    background-color: var(--mantine-color-dark-7);
  }
    .NavbarLinksGroup-module__control__t-YWe:hover {
    color: var(--mantine-color-black);
  }
    [data-mantine-color-scheme='dark'] .NavbarLinksGroup-module__control__t-YWe:hover {
    color: var(--mantine-color-dark-0);
  }
}

  @media (hover: none) {

  .NavbarLinksGroup-module__control__t-YWe:active {
    background-color: var(--mantine-color-gray-0);
  }
    [data-mantine-color-scheme='dark'] .NavbarLinksGroup-module__control__t-YWe:active {
    background-color: var(--mantine-color-dark-7);
  }
    .NavbarLinksGroup-module__control__t-YWe:active {
    color: var(--mantine-color-black);
  }
    [data-mantine-color-scheme='dark'] .NavbarLinksGroup-module__control__t-YWe:active {
    color: var(--mantine-color-dark-0);
  }
}

